body {
    font-family: Arial, sans-serif;
    line-height: 1.6;
    color: #333;
    margin: 0;
    padding: 0;
    background-color: #f4f4f4;
  }
  
  .main {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
  }
  
  h1 {
    text-align: center;
    color: #2c3e50;
  }
  header{
    display: "flex";
    justifyContent: "space-between";
    alignItems: "center";
    padding: "16px";
  }
  
  /* Form Styles */
  .input {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    margin-bottom: 30px;
    flex-direction: column;
    width: 100%;
    max-width: 300px;
  }
  
  label {
    margin-bottom: 5px;
    font-weight: bold;
  }
  
  input[type="number"] {
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 16px;
  }
  
  button {
    padding: 10px 20px;
    background-color: #3498db;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s;
  }
  
  button:hover {
    background-color: #2980b9;
  }
  
  /* Table Styles */
  table {
    border-collapse: collapse;
    width: 100%;
    background-color: white;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  }
  
  th, td {
    border: 1px solid #ddd;
    padding: 12px;
    text-align: left;
  }
  
  thead th {
    background-color: #3498db;
    color: white;
    font-weight: bold;
  }
  
  tbody th {
    background-color: #ecf0f1;
    font-weight: bold;
  }
  
  /* Cell Styles */
  input[type="text"] {
    width: 100%;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 14px;
  }
  
  .generate-button {
    margin-top: 5px;
    padding: 5px 10px;
    font-size: 12px;
    background-color: #2ecc71;
  }
  
  .generate-button:hover {
    background-color: #27ae60;
  }
  
  .formula-result {
    margin-top: 5px;
    font-size: 12px;
    color: #7f8c8d;
  }
  
  /* Responsive Styles */
  @media screen and (max-width: 768px) {
    table {
      font-size: 14px;
    }
  
    th, td {
      padding: 8px;
    }
  
    input[type="text"] {
      font-size: 12px;
    }
  
    .generate-button {
      font-size: 10px;
      padding: 3px 6px;
    }
  }
  
  @media screen and (max-width: 480px) {
    table {
      font-size: 12px;
    }
  
    th, td {
      padding: 6px;
    }
  
    input[type="text"] {
      font-size: 10px;
    }
  
    .generate-button {
      font-size: 9px;
      padding: 2px 4px;
    }
  }
  
  /* Back Button */
  .back-button {
    margin-top: 20px;
    background-color: #e74c3c;
  }
  
  .back-button:hover {
    background-color: #c0392b;
  }
  